export default function $(selector, options = {}, context = document) {
  let els;
  let elements = [];
  const listeners = {};
  if (typeof selector === 'object') {
    elements = [].slice.call(selector);
  } else if (selector[0] === '<' && selector[selector.length - 1] === '>') {
    els = context.createElement(selector.split('').slice(1, selector.length - 1).join(''));
    if (options && typeof options === 'object' && Object.keys(options).length > 0) {
      Object.keys(options).forEach((opt) => {
        els.setAttribute(opt.toString(), `${options[opt]}`);
      });
    }
    elements.push(els);
  } else {
    els = context.querySelectorAll(selector);
    elements = [].slice.call(els);
  }

  return {
    elements,

    get(index) {
      return (index >= 0) ? this.elements[index] : this.elements;
    },

    html(html) {
      this.elements.forEach((element) => {
        const tmp = element;
        tmp.innerHTML = html;
      });
      return this;
    },

    addClass(newClass) {
      this.elements.forEach((element) => {
        element.classList.add(newClass);
      });
      return this;
    },

    removeClass(rClass) {
      this.elements.forEach((element) => {
        element.classList.remove(rClass);
      });
      return this;
    },

    find(innerSelector) {
      return $(innerSelector);
    },

    append(nodes) {
      this.elements.forEach((element) => {
        nodes.elements.forEach((node) => {
          element.appendChild(node);
        });
      });
      return this;
    },

    value() {
      return this.elements[0].value;
    },

    css(css, value) {
      if (typeof css === 'string') {
        if (!value) {
          return window.getComputedStyle(this.elements[0]).getPropertyValue(css);
        }
        this.elements.forEach((element) => {
          element.setAttribute('style', `${css}:${value}`);
        });
      } else if (typeof css === 'object' && !value) {
        this.elements.forEach((element) => {
          Object.keys(css).forEach((key) => {
            element.setAttribute('style', `${key}:${css[key]}`);
          });
        });
      } else {
        throw new Error('invalid parameters');
      }
      return this;
    },

    on(e, callback) {
      this.elements.forEach((element) => {
        listeners[e] = callback;
        element.addEventListener(e, callback);
      });
      return this;
    },

    off(e) {
      this.elements.forEach((element) => {
        const handler = listeners[e];
        element.removeEventListener(e, handler);
        delete listeners[e];
      });
      return this;
    },

  };
}
