export default class App {
  constructor(model, viewModel, view) {
    this.model = model;
    this.viewModel = viewModel;
    this.view = view;
  }

  setActions() {
    const { wrapper, list, pagination } = this.view;
    const {
      current, start, previos, next,
    } = pagination;
    const ttPrevios = previos.find('.tooltip-previos');
    const ttNext = next.find('.tooltip-next');
    let pageWidth = this.view.getRoot().clientWidth;
    let currentScroll = 0;
    let pageNumber = 1;
    current.html(pageNumber);
    ttNext.html(pageNumber + 1);
    wrapper.on('pointerdown', (downEvent) => {
      list.addClass('active');
      const listWidth = list.get(0).scrollWidth;
      const x = downEvent.clientX;
      const delta = x - currentScroll;
      wrapper.on('pointermove', (moveEvent) => {
        list.css({ left: `${moveEvent.clientX - delta}px` });
      });
      wrapper.on('pointerup', (upEvent) => {
        App.doDefault(upEvent);
        list.removeClass('active');
        wrapper.off('pointermove').off('pointerup');
        if (upEvent.clientX - x === 0) {
          return;
        }
        if (upEvent.clientX - x > 0) {
          currentScroll = (currentScroll + pageWidth > 0) ? 0 : currentScroll + pageWidth;
          list.css({ left: `${currentScroll}px` });
          pageNumber = App.setPagePosition(
            ttPrevios, ttNext, current, previos, pageNumber, true,
          );
        } else {
          list.css({ left: `${currentScroll -= pageWidth}px` });
          pageNumber = App.setPagePosition(
            ttPrevios, ttNext, current, previos, pageNumber, false,
          );
        }
        if (listWidth <= -currentScroll + (pageWidth * 3)) {
          this.loadNextItems();
        }
      });
    }).on('dragstart', (dragEvent) => {
      App.doDefault(dragEvent);
    });
    current.on('pointerdown', (pCurrent) => {
      App.doDefault(pCurrent);
    });
    start.on('pointerdown', (pStart) => {
      App.doDefault(pStart);
      list.css({ left: `${currentScroll = 0}px` });
      pageNumber = App.setPagePosition(
        ttPrevios, ttNext, current, previos, 1, true,
      );
      list.css({ left: 0 });
      current.html(pageNumber = 1);
    });
    previos.on('pointerdown', (pPrev) => {
      App.doDefault(pPrev);
      list.css({ left: `${currentScroll += pageWidth}px` });
      pageNumber = App.setPagePosition(
        ttPrevios, ttNext, current, previos, pageNumber, true,
      );
    });
    next.on('pointerdown', (pNext) => {
      App.doDefault(pNext);
      list.css({ left: `${currentScroll -= pageWidth}px` });
      const listWidth = list.get(0).scrollWidth;
      pageNumber = App.setPagePosition(
        ttPrevios, ttNext, current, previos, pageNumber, false,
      );
      if (listWidth <= -currentScroll + (pageWidth * 3)) {
        this.loadNextItems();
      }
    });
    window.addEventListener('resize', () => {
      if (currentScroll !== 0) {
        const oldWidth = pageWidth;
        const oldNumber = currentScroll / pageWidth;
        pageWidth = this.view.getRoot().clientWidth;
        let number = (oldNumber * (oldWidth / pageWidth)).toFixed(0);
        if (oldWidth !== pageWidth) {
          number = (oldWidth > pageWidth) ? +number - 1 : +number + 1;
        }
        currentScroll = number * pageWidth;
        list.css({ left: `${currentScroll}px` });
        pageNumber = -(+number - 1);
        current.html(pageNumber);
        ttPrevios.html(pageNumber - 1);
        ttNext.html(pageNumber + 1);
      } else {
        pageWidth = this.view.getRoot().clientWidth;
      }
    });
  }

  loadNextItems() {
    this.model.getNextItems().then((result) => {
      window.console.log(result);
      this.view.render(this.viewModel.prepareData(result));
    }).catch((err) => {
      window.console.log(err);
    });
  }

  static setPagePosition(tPrev, tNext, current, previos, pageNumber, direction) {
    let currentPosition = pageNumber;
    if (direction) {
      currentPosition = (currentPosition === 1) ? currentPosition : currentPosition - 1;
      current.html(currentPosition);
    } else {
      current.html(currentPosition += 1);
    }
    tPrev.html(currentPosition - 1);
    tNext.html(currentPosition + 1);
    if (currentPosition > 2) {
      previos.removeClass('not-active');
    } else {
      previos.addClass('not-active');
    }
    return currentPosition;
  }

  static doDefault(e) {
    e.preventDefault();
    e.stopPropagation();
  }

  search() {
    this.view.form.on('submit', (event) => {
      App.doDefault(event);
      this.model.getItems(this.view.search.value())
        .then((result) => {
          this.view.render(this.viewModel.prepareData(result));
          this.view.addPagination();
          this.setActions();
        }).catch((err) => {
          window.console.log(err);
        });
    });
    this.view.form.on('mousedown', (e) => {
      e.stopPropagation();
    });
  }

  start() {
    this.view.render();
    this.search();
  }
}
