export default class AppModel {
  constructor(key) {
    this.url = 'https://www.googleapis.com/youtube/v3/';
    this.key = key;
    this.pagination = {
      nextToken: '',
      prevToken: '',
    };
    this.options = {
      type: 'video',
      part: 'snippet',
      maxResults: '15',
      q: '',
    };
  }

  getRequestString(requestType, options) {
    let str = `${this.url + requestType}?key=${this.key}`;
    Object.keys(options).forEach((key) => {
      str += `&${key}=${options[key]}`;
    });
    return str;
  }

  getItems(query) {
    const opts = {
      type: 'video',
      part: 'snippet',
      maxResults: '15',
      q: query,
    };
    return new Promise((resolve, reject) => {
      fetch(this.getRequestString('search', opts))
        .then(response => response.json())
        .then((result) => {
          const { items } = result;
          this.pagination.nextToken = result.nextPageToken;
          this.getItemsStatistics(AppModel.getItemsIds(items))
            .then((statistic) => {
              this.options = opts;
              resolve({
                videos: items,
                statistics: statistic.items,
              });
            }).catch(err => reject(err));
        })
        .catch(err => reject(err));
    });
  }

  getItemsStatistics(ids) {
    const opts = {
      id: ids,
      part: 'snippet, statistics',
    };
    return new Promise((resolve, reject) => {
      fetch(this.getRequestString('videos', opts))
        .then(response => response.json())
        .then(result => resolve(result))
        .catch(err => reject(err));
    });
  }

  static getItemsIds(items) {
    return items.map(item => item.id.videoId);
  }

  search(query) {
    this.options.q = query;
    return this.getItems();
  }

  getNextItems() {
    const nextRequest = `${this.getRequestString('search', this.options)}&pageToken=${this.pagination.nextToken}`;
    return new Promise((resolve, reject) => {
      fetch(nextRequest)
        .then(response => response.json())
        .then((result) => {
          const { items } = result;
          this.pagination.nextToken = result.nextPageToken;
          this.getItemsStatistics(AppModel.getItemsIds(items))
            .then((statistic) => {
              resolve({
                videos: items,
                statistics: statistic.items,
              });
            }).catch(err => reject(err));
        })
        .catch(err => reject(err));
    });
  }
}
