export default class AppViewModel {
  constructor(data) {
    this.data = data;
  }

  getData() {
    return this.data;
  }

  prepareData(data) {
    this.data = data;
    return data.videos.map((item, i) => {
      const { id, snippet } = item;
      const {
        publishedAt, channelId, channelTitle, title, description, thumbnails,
      } = snippet;
      const { viewCount, likeCount, dislikeCount } = data.statistics[i].statistics;
      return {
        videoId: id.videoId,
        videoTitle: title,
        owner: channelTitle,
        ownerId: channelId,
        videoDescription: description,
        uploaded: publishedAt.split('').slice(0, 10).join(''),
        views: viewCount,
        likes: likeCount,
        dislikes: dislikeCount,
        thumbnailUrl: thumbnails.default.url,
        thumbnailSize: {
          width: thumbnails.default.width,
          height: thumbnails.default.height,
        },
      };
    });
  }
}
