import $ from './helpers/domHelper';
import './font-awesome/css/font-awesome.min.css';
import './css/styles.css';

export default class appView {
  constructor(container) {
    let rootElement;
    if (typeof container === 'string') {
      rootElement = $(container);
    } else {
      [rootElement] = container;
    }
    this.wrapper = $('<div>', { class: 'wrapper' });
    this.form = $('<form>', { class: 'search-form', name: 'search' });
    this.search = $('<input>', {
      class: 'search-text', type: 'text', name: 'search-input', placeholder: 'Ask YouTube',
    });
    this.list = $('<ul>', { class: 'search-list-container' });
    this.submit = $('<button>', { class: 'fa fa-search submit-button', type: 'submit', value: '' });
    this.pagination = {
      start: $('<li>', { class: 'pagination-item pagination-start' }).append($('<i>', { class: 'fa fa-angle-double-left' })).append($('<a>', { class: 'pagination-link link-start', href: '#1' }).append($('<span>', { class: 'pagination-tooltip tooltip-start' }).html('1'))),
      previos: $('<li>', { class: 'pagination-item pagination-previos not-active' }).append($('<i>', { class: 'fa fa-angle-left' })).append($('<a>', { class: 'pagination-link link-previos', href: '#previos' }).append($('<span>', { class: 'pagination-tooltip tooltip-previos' }))),
      current: $('<li>', { class: 'pagination-item pagination-current' }),
      next: $('<li>', { class: 'pagination-item pagination-next' }).append($('<i>', { class: 'fa fa-angle-right' })).append($('<a>', { class: 'pagination-link link-next', href: '#next' }).append($('<span>', { class: 'pagination-tooltip tooltip-next' }))),
    };
    this.container = rootElement;
  }

  createDefaultTemplate() {
    this.form.append(this.search);
    this.form.append(this.submit);
    this.wrapper.append(this.form);
    this.wrapper.append(this.list);
    this.container.append(this.wrapper);
  }

  addPagination() {
    const pagContainer = $('<ul>', { class: 'pagination-container' }).append(
      this.pagination.start,
    ).append(
      this.pagination.previos,
    ).append(
      this.pagination.current,
    )
      .append(
        this.pagination.next,
      );
    this.wrapper.append(pagContainer);
  }

  getRoot() {
    return this.container.get(0);
  }

  static createCards(item) {
    const cardContainer = $('<li>', { class: 'card-item-container' });
    const cardThumb = $('<img>', { class: 'card-item-thumb', src: item.thumbnailUrl });
    const cardUrl = $('<a>', { class: 'card-item-link', href: `https://www.youtube.com/watch?v=${item.videoId}` }).html(item.videoTitle);
    const cardDescription = $('<p>', { class: 'card-item-description' }).html(item.videoDescription);
    const cardInfo = $('<p>', { class: 'card-item-info' })
      .append($('<div>', { class: 'card-item-info-left' })
        .append($('<i>', { class: 'fa fa-user info-channel' }))
        .append($('<i>', { class: 'fa fa-calendar info-upload-date' }))
        .append($('<i>', { class: 'fa fa-eye info-views' }))).append($('<div>', { class: 'card-item-info-right' })
        .append($('<a>', { class: 'info-video-owner', href: `https://www.youtube.com/channel/${item.ownerId}` }).html(item.owner))
        .append($('<span>', { class: 'info-upload-date-text' }).html(item.uploaded))
        .append($('<span>', { class: 'info-views-count' }).html(item.views)));
    const cardFooter = $('<p>', { class: 'card-item-footer' })
      .append($('<div>', { class: 'card-item-footer-left' })
        .append($('<i>', { class: 'fa fa-thumbs-up footer-likes-icon' }))
        .append($('<span>', { class: 'footer-likes' }).html(item.likes)))
      .append($('<div>', { class: 'card-item-footer-right' })
        .append($('<i>', { class: 'fa fa-thumbs-down footer-dislikes-icon' }))
        .append($('<span>', { class: 'footer-dislikes' }).html(item.dislikes)));
    cardContainer
      .append(cardThumb)
      .append(cardUrl)
      .append(cardInfo)
      .append(cardDescription)
      .append(cardFooter);
    return cardContainer;
  }

  render(data) {
    if (data && data.length > 0) {
      data.forEach((item) => {
        this.list.append(appView.createCards(item));
      });
    } else {
      this.createDefaultTemplate();
    }
  }
}
